                <n-checkbox v-model:checked="checkboxSkillEffects[3]" :disabled="!disabledCheck[1]" @update:checked="this.handleUpdateSelect()">S3</n-checkbox>
              </div>
            </div>
            <div id="filteradv_modal_section_char">
              <h3 style="width: 100%">Combination Skill</h3>
              <n-select
                v-model:value="selectedCombination"
                filterable
                placeholder="Combination Skill"
                :options=skill_comb.combination
                style="width: 100%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            </div>
            <div id="filteradv_modal_section_char">
              <h3 style="width: 100%">Development Skill</h3>
              <n-select
                v-model:value="selectedDevelopment[0]"
                filterable
                placeholder="Dev. Names"
                :options=skill_dev.names
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="selectedDevelopment[1]"
                filterable
                placeholder="Dev. Effects"
                :options=skill_dev.effects
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-input-number
                v-model:value="selectedDevelopment[2]"
                placeholder="Dev. Min Val"
                :step="5"
                min = "0"
                max = "150"
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
              > <template #suffix>
                  %
                </template>
              </n-input-number>
              <n-input-number
                v-model:value="selectedDevelopment[3]"
                placeholder="Dev. Max Val"
                :step="5"
                min = "0"
                max = "150"
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
              > 
                <template #suffix>
                  %
                </template>
              </n-input-number>
            </div>
            <div id="filteradv_modal_section_char">
              <h3 style="width: 100%">Extra Info:</h3>
              <n-select
                v-model:value="sortBy"
                filterable
                placeholder="Sort by:"
                :options=skill_sort.sort
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="sortAsc"
                filterable
                placeholder="Order"
                :options=skill_sort.asc
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="displayInfo"
                filterable
                placeholder="Display Info"
                :options=skill_sort.display
                style="width: 98%"
                @update:value="updateDisplay"
                clearable
                :consistent-menu-width="false"
              />
            </div>
            <div id="filteradv_modal_section_button">
              <!-- <h3 style="width: 100%">Sort by</h3> -->
              <n-button 
                type="error"
                style="width: 30%; height: 80px"
                @click="clearFilter()"
              >
                Reset
              </n-button>
              <n-button 
                type="primary"
                style="width: 68%; margin-left: 2%; height: 80px"
                @click="searchFilter()"
              >
                Search
              </n-button>
            </div>
          </n-scrollbar>
        </n-card>
      </n-modal>
    </div>
    <n-scrollbar class="main_outlist" :style="screenWidth >= 768 ? {height: 'calc(100% - 950px)'} : {height: 'calc(100% - 202px)'}" > 
      <div class="main_inlist">
        <div v-if="dataAdv.length == 0">
          <p>
            Loading...
          </p>
        </div>
        <div id="list_empty_message" v-if="filteredData.length == 0 && !isFirstSearch">
          <h3>
            So empty...
          </h3>
          <p>
            This search found nothing
          </p>
        </div>
        <n-grid :x-gap="0" :y-gap="0" :cols="n_columns" >
          <n-grid-item v-for="char in filteredData" :key="char.id_order" class="cell">
            <!-- <n-tooltip trigger="hover">
              <template #trigger> -->
                <div class="imagecell" @click='gotoCharPage(char.id_adventurers)' :title="'[' + char.title + '] ' + char.name">
                  <img alt="Frame" :src="decideBase(char.stars, char.have_hero_asc, char.hero_festa)" >
                  <!-- <img :alt="char.id_intern" :src="getImg(char.id_intern)" class="char_img"> -->
                  <img alt="Frame_front" :src="decideBaseFrame(char.stars, char.have_hero_asc, char.hero_festa)">
                  <div id="display_info" v-if="showDisplay">
                    <span style="color: white">{{ getDisplay(char) }}</span> 
                    <!-- :style="chooseStyle(char)" -->
                  </div>
                </div>
              <!-- </template>
              <span>[{{ char.title }}]<br />{{ char.name }}</span>
            </n-tooltip> -->
          </n-grid-item>
        </n-grid>
      </div>
    </n-scrollbar>
  </div>
</template>

<script>
import { 
  NScrollbar, 
  NGrid, 
  NGridItem, 
  NButton, 
  NTooltip,
  NSelect,
  NCheckbox,
  NSpace,
  NSwitch,
  NInputNumber,
  NModal,
  NInput,
  NCard
} from 'naive-ui'
import axios from 'axios'
import {ref} from 'vue'

export default {
  components: { 
    NScrollbar,
    NGrid, 
    NGridItem, 
    NButton, 
    NTooltip,
    NSelect,
    NCheckbox,
    NSpace,
    NSwitch,
    NInputNumber,
    NInput,
    NModal,
    NCard
  },
  data() {
    return {
      dataAdv: [],
      filteredData: [],

      isFirstSearch: true,

      filterModal: ref(null),
      // Char, Stars, TimeLimited, isFree, Attack Type, Element, HeroAscension
      selectedChar: ref([null, null, null, null, null, null, null]),
      // Targets, Speed, Power, Element, Attack Type, Boost, Modifier, Index
      selectedAttack: ref([null, null, null, null, null, null, null, null]),
      // HasPerEach, Per Each
      selecterPerEach: ref([null, null]),
      // Target, Effects, Value1, Value2
      selectedEffects: ref([null, null, null, null]),
      // Combination
      selectedCombination: ref(null),
      // N_repeat, repeathasattack, repeathaseffect
      selectedRepeat: ref([null, null, null]),
      // Include repeat in search for attacks or actions
      selectedIncludeRepeat: ref([null, null]),
      // Name, Effect, Val1, Val2
      selectedDevelopment: ref([null, null, null, null]),
      // Killer
      selectedKiller: ref(null),

      checkboxSkill: [true, true, true, true],
      checkboxSkillEffects: [true, true, true, true],
      disabledCheck: [false, false],

      sortBy: ref(null),
      sortAsc: ref(null),

      displayInfo: ref(null),
      showDisplay: false,

      effectTurns: false,

      scrollTopVisible: false,

      screenWidth: window.innerWidth,
      displayColor: "",

      n_columns: 9,

      skill_arguments: null,
      skill_comb: null,
      skill_char: null,
      skill_sort: null,
      skill_dev: null
      // tooltips_text: null
    }
  },
  beforeMount() {
    var Skills = require('../composables/constants/filter_skills_info.json')
    var SkillsComb = require('../composables/constants/filter_combination_info.json')
    var SkillsSort = require('../composables/constants/filter_sort_info.json')
    var SkillsChar = require('../composables/constants/filter_attack_info.json')
    var SkillsDev = require('../composables/constants/filter_dev_info.json')

    let adv_list = window.sessionStorage.getItem("adv_list")
    if(adv_list == null){
      var data = require('../data/adv_list_DATA.json')
      this.dataAdv = data
      this.filteredData = data
      window.sessionStorage.setItem("adv_list", JSON.stringify(data));
    }else{
      this.dataAdv = JSON.parse(adv_list)
      this.filteredData = JSON.parse(adv_list)
      // console.log(this.$route.query)
    }

    let query = this.$route.query
    // console.log(query)
    if(Object.keys(query).length != 0){
      this.isFirstSearch = false
      this.selectedChar[0] = query.char
      this.selectedChar[1] = query.star
      this.selectedChar[2] = query.timelimited
      this.selectedChar[3] = query.free
      this.selectedChar[4] = query.attacktype
      this.selectedChar[5] = query.element
      this.selectedChar[6] = query.heroasc

      this.selectedAttack[0] = query.targets
      this.selectedAttack[1] = query.speed
      this.selectedAttack[2] = query.power
      this.selectedAttack[3] = query.element_atk
      this.selectedAttack[4] = query.type
      this.selectedAttack[5] = query.boost
      this.selectedAttack[6] = query.modifier
      this.selectedAttack[7] = query.index

      this.selecterPerEach[0] = query.has_pereach
      this.selecterPerEach[1] = query.pereach_ef

      this.selectedEffects[0] = query.ef_target
      this.selectedEffects[1] = query.ef
      this.selectedEffects[2] = query.ef_val1 ? parseInt(query.ef_val1) : null
      this.selectedEffects[3] = query.ef_val2 ? parseInt(query.ef_val2) : null

      this.selectedCombination = query.comb_skill
      this.selectedKiller = query.killer

      this.selectedDevelopment[0] = query.devname
      this.selectedDevelopment[1] = query.devskill
      this.selectedDevelopment[2] = query.dev_valmin ? parseInt(query.dev_valmin) : null 
      this.selectedDevelopment[3] = query.dev_valmax ? parseInt(query.dev_valmax) : null

      this.selectedRepeat[0] = query.n_repeat
      this.selectedRepeat[1] = query.repeat_hasattack
      this.selectedRepeat[2] = query.repeat_haseffect
      
      this.selectedIncludeRepeat[0] = query.filter_repeat_attack
      this.selectedIncludeRepeat[1] = query.filter_repeat_effect
      
      this.disabledCheck[0] = query.search_attack != null ? true : false
      this.disabledCheck[1] = query.search_effect != null ? true : false

      this.checkboxSkill[0] = query.search_attack != null ? (query.search_attack.indexOf("s") > -1 ? true : false) : true
      this.checkboxSkill[1] = query.search_attack != null ? (query.search_attack.indexOf("1") > -1 ? true : false) : true
      this.checkboxSkill[2] = query.search_attack != null ? (query.search_attack.indexOf("2") > -1 ? true : false) : true
      this.checkboxSkill[3] = query.search_attack != null ? (query.search_attack.indexOf("3") > -1 ? true : false) : true
      this.checkboxSkillEffects[0] = query.search_effect != null ? (query.search_effect.indexOf("s") > -1 ? true : false) : true
      this.checkboxSkillEffects[1] = query.search_effect != null ? (query.search_effect.indexOf("1") > -1 ? true : false) : true
      this.checkboxSkillEffects[2] = query.search_effect != null ? (query.search_effect.indexOf("2") > -1 ? true : false) : true
      this.checkboxSkillEffects[3] = query.search_effect != null ? (query.search_effect.indexOf("3") > -1 ? true : false) : true
      this.sortBy = query.sort
      this.sortAsc = query.asc
      this.displayInfo = query.display
    }

    if(query.display != null){
      this.showDisplay = true;
    }

    // this.searchFilter()
    this.onResize()

    this.skill_arguments = Skills
    this.skill_comb = SkillsComb
    this.skill_char = SkillsChar
    this.skill_sort = SkillsSort
    this.skill_dev = SkillsDev
    // this.tooltips_text = 

    this.searchFilter()
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
      // window.addEventListener('scroll', this.scrollListener)
    })
  },

  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
    // window.removeEventListener('scroll', this.scrollListener)
  },
  methods: {
    onResize() {
      this.screenWidth = window.innerWidth
      if(this.screenWidth > 1000){
        this.n_columns = 9
        this.filterModal = false
      }else if(this.screenWidth > 785){
        this.n_columns = 7
        this.filterModal = false
      }else if(this.screenWidth > 600){
        this.n_columns = 5
      }else{
        this.n_columns = 3
      }
    },
    // scrollListener(e) {
    //   this.scrollTopVisible = window.scrollY > 150
    // },
    updateDisplay(){
      if(this.displayInfo != null){
        this.showDisplay = true
        this.displayColor = this.displayInfo
      }else{
        this.showDisplay = false
      }
      this.handleUpdateSelect()
    },
    getDisplay(char){
      let query = this.$route.query;
      if(query.display && char){
        switch(query.display){
          case "char_type":
            return this.convertAttackType(char.type_attack)
          case "char_element":
            return char.type_element.charAt(0).toUpperCase() + char.type_element.slice(1)
          case "char_stars":
            return char.stars
          case "char_time_limited":
            return char.time_limited ? "Yes" : "No"
          case "char_free_unit":
            return char.is_free ? "Yes" : "No"
          case "char_hero_asc":
            return char.have_hero_asc ? "Yes" : "No"
          case "char_killer":
            return char.killer.charAt(0).toUpperCase() + char.killer.slice(1).split("_")[0]
          case "char_strength":
            return char.str
          case "char_endurance":
            return char.end
          case "char_dexterity":
            return char.dex
          case "char_agility":
            return char.agi
          case "char_magic":
            return char.mag
          case "char_hp":
            return char.hp
          case "char_mp":
            return char.mp
          case "char_max_strength":
            return char.max_str
          case "char_max_endurance":
            return char.max_end
          case "char_max_dexterity":
            return char.max_dex
          case "char_max_agility":
            return char.max_agi
          case "char_max_magic":
            return char.max_mag
          case "char_max_hp":
            return char.max_hp
          case "char_max_mp":
            return char.max_mp
          case "effect_value":
            return this.searchEffectValue(char, query, "value")
          case "effect_turns":
            return this.searchEffectValue(char, query, "turns")
          case "dev_value":
            return this.searchDevValue(char, query)
          
          default:
            return char.name
        }
      }
    },
    searchEffectValue(char, query, choose){
      if(query.ef){
        let combat = []
        if(query.search_effect != null){
          if(query.search_effect.indexOf("s") > -1)
            combat.push("special")
          if(query.search_effect.indexOf("1") > -1)
            combat.push("skill1")
          if(query.search_effect.indexOf("2") > -1)
            combat.push("skill2")
          if(query.search_effect.indexOf("3") > -1)
            combat.push("skill3")
        }else{
          combat = ["special", "skill1", "skill2", "skill3"]
        }
        let highestValue = -200
        let highestString = ""
        for(let i = 0; i < combat.length; i++){
          if(Object.keys(char.combat_skills[combat[i]].effects).length != 0){
            for(let j = 0; j < char.combat_skills[combat[i]].effects.n_effects; j++){
              if(char.combat_skills[combat[i]].effects[(j+1).toString() + "effect"].toLowerCase().replace(/ /g, "_") == query.ef){
                let skilloutput = this.convertSkillNotation(combat[i])
                if(query.ef_target){
                  if(char.combat_skills[combat[i]].effects[(j+1).toString() + "targets"].toLowerCase() == query.ef_target){
                    if((parseInt(char.combat_skills[combat[i]].effects[(j+1).toString() + choose])) > highestValue){
                      highestValue = parseInt(char.combat_skills[combat[i]].effects[(j+1).toString() + choose])
                      highestString = skilloutput + char.combat_skills[combat[i]].effects[(j+1).toString() + choose]
                    }
                  }
                }else{
                  if((parseInt(char.combat_skills[combat[i]].effects[(j+1).toString() + choose])) > highestValue){
                    highestValue = parseInt(char.combat_skills[combat[i]].effects[(j+1).toString() + choose])
                    highestString = skilloutput + char.combat_skills[combat[i]].effects[(j+1).toString() + choose]
                  }
                }
              }
            }
          }
          if(Object.keys(char.combat_skills[combat[i]].repeat_attack).length != 0){
            if(Object.hasOwn(char.combat_skills[combat[i]].repeat_attack, "effects")){
              for(let j = 0; j < char.combat_skills[combat[i]].repeat_attack.effects.n_effects; j++){
                if(char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + "effect"].toLowerCase().replace(/ /g, "_") == query.ef){
                  let skilloutput = this.convertSkillNotation(combat[i])
                  if(query.ef_target){
                    if(char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + "targets"].toLowerCase() == query.ef_target){
                      // return skilloutput + char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + choose]
                      if((parseInt(char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + choose])) > highestValue){
                        highestValue = parseInt(char.combat_skills[combat[i]].effects[(j+1).toString() + choose])
                        highestString = skilloutput + char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + choose]
                      }
                    }
                  }else{
                    if((parseInt(char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + choose])) > highestValue){
                      highestValue = parseInt(char.combat_skills[combat[i]].effects[(j+1).toString() + choose])
                      highestString = skilloutput + char.combat_skills[combat[i]].repeat_attack.effects[(j+1).toString() + choose]
                    }
                  }
                }
              }
            }
          }
        }
        return highestString
      }
    },
    searchDevValue(char, query){
      if(query.devskill){
        let highestValue = -200
        let highestSuffix = ""
        for(let i = 0; i < char.dev_abilities.length; i++){
          for(let j = 0; j < char.dev_abilities[i].n_efeitos; j++){
            if(char.dev_abilities[i]["effect_" + (j+1).toString()].toLowerCase().replace(/ /g, "_") == query.devskill){
              let temp = parseInt(char.dev_abilities[i]["value_" + (j+1).toString()])
              if(temp > highestValue){
                highestValue = temp
                if(char.dev_abilities[i]["value_" + (j+1).toString()].indexOf('%') > -1){
                  highestSuffix = "%"
                }
              }
            }
          }
        }
        // if(char.dev_abilities[i]["value_" + (j+1).toString()].indexOf('+'))
        if(highestValue == -200){
          return ""
        }
        return highestValue + highestSuffix
      }
    },
    convertAttackType(char){
      switch(char){
        case "physical_type":
          return "Physical Type"
        case "magic_type":
          return "Magic Type"
        case "bala_phys":
          return "Balanced Type"
        case "bala_mag":
          return "Balanced Type"
        case "balance_type":
          return "Balanced Type"
        case "balance_type_old":
          return "Balanced Type"
        case "defense_type":
          return "Defense Type"
        case "healer_type":
          return "Healer Type"

        // PLUS
        case "phys_plus":
          return "Physical Type"
        case "mag_plus":
          return "Magic Type"
        case "bala_plus_phys":
          return "Balanced Type"
        case "bala_plus_mag":
          return "Balanced Type"

        // Festa
        case "phys_festa":
          return "Physical Type"

        default:
          return ""
      }
    },
    convertSkillNotation(skill){
      switch(skill){
        case "special":
          return "SP: "
        case "skill1":
          return "S1: "
        case "skill2":
          return "S2: "
        case "skill3":
          return "S3: "
        default:
          return ""
      }
    },
    chooseStyle(char){
      switch(this.displayColor){
        case ("char_time_limited"):
          if(char.time_limited == true){
            return {color: "red", fontWeight: "bold"}
          }else{
            return {color: "white"}
          }
        case ("char_free_unit"):
          if(char.is_free == true){
            return {color: "red", fontWeight: "bold"}
          }else{
            return {color: "white"}
          }
        default:
          return {color: "white"}
      }
    },
    handleUpdateSelect(){
      let filter = {
        targets: this.selectedAttack[0],
        speed: this.selectedAttack[1],
        power: this.selectedAttack[2],
        element_atk: this.selectedAttack[3],
        type: this.selectedAttack[4],
        boost: this.selectedAttack[5],
        modifier: this.selectedAttack[6],
        index: this.selectedAttack[7],
        ef_target: this.selectedEffects[0],
        ef: this.selectedEffects[1],
        ef_val1: this.selectedEffects[2],
        ef_val2: this.selectedEffects[3],
        comb_skill: this.selectedCombination,
        killer: this.selectedKiller,
        char: this.selectedChar[0],
        star: this.selectedChar[1],
        timelimited: this.selectedChar[2],
        free: this.selectedChar[3],
        attacktype: this.selectedChar[4],
        element: this.selectedChar[5],
        heroasc: this.selectedChar[6],
        devname: this.selectedDevelopment[0],
        devskill: this.selectedDevelopment[1],
        dev_valmin: this.selectedDevelopment[2],
        dev_valmax: this.selectedDevelopment[3],
        n_repeat: this.selectedRepeat[0],
        repeat_hasattack: this.selectedRepeat[1],
        repeat_haseffect: this.selectedRepeat[2],
        has_pereach: this.selecterPerEach[0],
        pereach_ef: this.selecterPerEach[1],
        filter_repeat_attack: this.selectedIncludeRepeat[0],
        filter_repeat_effect: this.selectedIncludeRepeat[1],
        search_attack: this.disabledCheck[0] ? ((this.checkboxSkill[0] ? "s" : "") + (this.checkboxSkill[1] ? "1" : "") + (this.checkboxSkill[2] ? "2" : "") + (this.checkboxSkill[3] ? "3" : "")) : null,
        search_effect: this.disabledCheck[1] ? ((this.checkboxSkillEffects[0] ? "s" : "") + (this.checkboxSkillEffects[1] ? "1" : "") + (this.checkboxSkillEffects[2] ? "2" : "") + (this.checkboxSkillEffects[3] ? "3" : "")) : null,
        sort: this.sortBy,
        asc: this.sortAsc,
        display: this.displayInfo
      }
      // console.log(filter.search_attack)

      if(this.selectedEffects[1]){
        if(this.selectedEffects[1].indexOf("turns") > -1){
          this.effectTurns = true
        }else{
          this.effectTurns = false
        }
      }

      // console.log()
      Object.keys(filter).forEach(key => {
        if (filter[key] === null || filter[key] === undefined) {
          delete filter[key];
        }
      });
      this.$router.replace({ name: 'filter_adv', query: filter })
    },
    getImg(url){
      return require('../assets/adventurers/hex_adv/' + url + '.png')
    },

    goBackChar(){
      this.$router.push({ name: "adventurer_list"});
    },

    gotoCharPage(char_id){
      this.$router.push({ name: "adventurer", params: {id: char_id} });
    },
    clearFilter(){
      this.selectedChar[0] = null
      this.selectedChar[1] = null
      this.selectedChar[2] = null
      this.selectedChar[3] = null
      this.selectedChar[4] = null
      this.selectedChar[5] = null
      this.selectedChar[6] = null
      this.selectedAttack[0] = null
      this.selectedAttack[1] = null
      this.selectedAttack[2] = null
      this.selectedAttack[3] = null
      this.selectedAttack[4] = null
      this.selectedAttack[5] = null
      this.selectedAttack[6] = null
      this.selectedAttack[7] = null
      this.selectedEffects[0] = null
      this.selectedEffects[1] = null
      this.selectedEffects[2] = null
      this.selectedEffects[3] = null
      this.selectedCombination = null
      this.selectedKiller = null
      this.selectedDevelopment[0] = null
      this.selectedDevelopment[1] = null
      this.selectedDevelopment[2] = null
      this.selectedDevelopment[3] = null
      this.sortBy = null
      this.sortAsc = null
      this.displayInfo = null
      this.selecterPerEach[0] = null
      this.selecterPerEach[1] = null
      this.selectedRepeat[0] = null
      this.selectedRepeat[1] = null
      this.selectedRepeat[2] = null
      this.selectedIncludeRepeat[0] = null
      this.selectedIncludeRepeat[1] = null
      this.checkboxSkill = [true, true, true, true]
      this.checkboxSkillEffects = [true, true, true, true]
      this.disabledCheck = [false, false]

      this.$router.replace({'query': null});
    },

    filterAttack(char, query, stringfilter){
      if(query.targets || query.speed || query.power || query.element_atk || query.type || query.boost || query.modifier || query.index || query.filter_repeat_attack){
        let temp = true;
        if(Object.keys(char.combat_skills[stringfilter].attack).length != 0){
          temp &&= (query.targets ? char.combat_skills[stringfilter].attack.attack_target.toLowerCase() == query.targets : true)
          temp &&= (query.speed ? char.combat_skills[stringfilter].attack.attack_velocity.toLowerCase() == query.speed : true)
          temp &&= (query.power ? char.combat_skills[stringfilter].attack.attack_power.toLowerCase() == query.power : true)
          temp &&= (query.element_atk ? char.combat_skills[stringfilter].attack.attack_element.toLowerCase() == query.element_atk : true)
          temp &&= (query.type ? char.combat_skills[stringfilter].attack.attack_type.toLowerCase() == query.type : true)
          temp &&= (query.boost ? char.combat_skills[stringfilter].attack.with_temp.toLowerCase().replace(". ", "_").replace(" ", "_") == query.boost : true)
          temp &&= (query.modifier ?
                  char.combat_skills[stringfilter].attack.has_attack_mod ?
                    char.combat_skills[stringfilter].attack.attack_modifier1.toLowerCase().replace(/ /g, "_") == query.modifier
                  : false
                : true)
          temp &&= (query.index ? 
                  char.combat_skills[stringfilter].attack.has_index ?
                    char.combat_skills[stringfilter].attack.index_2 ?
                      (char.combat_skills[stringfilter].attack.index_1.toLowerCase().replace(" ", "_") == query.index ||
                      char.combat_skills[stringfilter].attack.index_2.toLowerCase().replace(" ", "_") == query.index) :
                      (char.combat_skills[stringfilter].attack.index_1.toLowerCase().replace(" ", "_") == query.index)
                  : false
                : true)
          // if(char.id_adventurers=="bell_11"){
          //   console.log(char.combat_skills[stringfilter].attack.attack_modifier1, char, query)
          // }
          if(query.filter_repeat_attack == "atk"){
            return temp
          }
        } else {
          temp = false;
        }
        let temp2 = true
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0 && char.combat_skills[stringfilter].repeat_attack.repeat_has_attack){
          temp2 &&= (query.targets ? char.combat_skills[stringfilter].repeat_attack.attack_target.toLowerCase() == query.targets : true)
          temp2 &&= (query.speed ? char.combat_skills[stringfilter].repeat_attack.attack_velocity.toLowerCase() == query.speed : true)
          temp2 &&= (query.power ? char.combat_skills[stringfilter].repeat_attack.attack_power.toLowerCase() == query.power : true)
          temp2 &&= (query.element_atk ? char.combat_skills[stringfilter].repeat_attack.attack_element.toLowerCase() == query.element_atk : true)
          temp2 &&= (query.type ? char.combat_skills[stringfilter].repeat_attack.attack_type.toLowerCase() == query.type : true)
          temp2 &&= (query.boost ? char.combat_skills[stringfilter].repeat_attack.with_temp.toLowerCase().replace(". ", "_").replace(" ", "_") == query.boost : true)
          temp2 &&= (query.modifier ?
                  char.combat_skills[stringfilter].repeat_attack.has_attack_mod ?
                    char.combat_skills[stringfilter].repeat_attack.attack_modifier1.toLowerCase().replace(/ /g, "_") == query.modifier
                  : false
                : true)
          temp2 &&= (query.index ? 
                  char.combat_skills[stringfilter].repeat_attack.has_index ?
                    char.combat_skills[stringfilter].repeat_attack.index_2 ?
                      (char.combat_skills[stringfilter].repeat_attack.index_1.toLowerCase().replace(" ", "_") == query.index ||
                      char.combat_skills[stringfilter].repeat_attack.index_2.toLowerCase().replace(" ", "_") == query.index) :
                      (char.combat_skills[stringfilter].repeat_attack.index_1.toLowerCase().replace(" ", "_") == query.index)
                  : false
                : true)
          if(query.filter_repeat_attack == "repeat"){
            // Filter and get chars without attack
            return temp2
          }
        }else{
          temp2 = false;
        }
        
        temp ||= temp2

        if(Object.keys(char.combat_skills[stringfilter].attack).length == 0 && query.filter_repeat_attack == "atk"){
          return false
        }
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length == 0 && query.filter_repeat_attack == "repeat"){
          return false
        }
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0 
        && char.combat_skills[stringfilter].repeat_attack.repeat_has_attack == false
        && query.filter_repeat_attack == "repeat"
        ){
          return false
        }
        // both and null
        return temp
      }else{
        return true
      }
    },
    filterPerEach(char, query, stringfilter){
      if(query.pereach_ef){
        if(char.combat_skills[stringfilter].hasEach){
          if(char.id_adventurers == "aphrodite_1"){
            console.log(char.id_adventurers)
              }
          let temp = false;
          let temp1 = false;
          let temp2 = false;
          if(Object.keys(char.combat_skills[stringfilter].attack).length != 0){
            if(Object.hasOwn(char.combat_skills[stringfilter].attack), "perEachEffect_n"){
              for (let j = 0; j < char.combat_skills[stringfilter].attack.perEachEffect_n; j++){
    
                temp1 ||= char.combat_skills[stringfilter].attack["perEachEffect" + (j+1).toString()].toLowerCase().replace(/ /g, "_") == query.pereach_ef

              }
              if(query.filter_repeat_attack == "atk"){
                return temp1
              }
            }
          }else{
            if(query.filter_repeat_attack == "atk"){
              return temp1
            }
          }
          if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0){
            if(char.combat_skills[stringfilter].repeat_attack.perEachEffect_n){
              for (let j = 0; j < char.combat_skills[stringfilter].repeat_attack.perEachEffect_n; j++){

                temp2 ||= char.combat_skills[stringfilter].repeat_attack["perEachEffect" + (j+1).toString()].toLowerCase().replace(/ /g, "_") == query.pereach_ef

              }
              if(char.id_adventurers == "aphrodite_1"){
                console.log(temp2)
              }
              if(query.filter_repeat_attack == "repeat"){
                return temp2
              }
            }
          }else{
            if(query.filter_repeat_attack == "repeat"){
              return temp2
            }
          }
          temp = temp1 || temp2
          return temp
        }else{
          return false
        }
      }else{
        return true
      }
    },
    filterHasPerEach(char, query, stringfilter){
      if(query.has_pereach){
        let tempNormal = false
        let tempRepeat = false
        let output = false
        if(char.combat_skills.hasEach && (query.filter_repeat_attack == "both" || !query.filter_repeat_attack)){
          return true
        }
        if(Object.keys(char.combat_skills[stringfilter].attack).length != 0){
          if(Object.hasOwn(char.combat_skills[stringfilter].attack, "perEachEffect_n").toString() == query.has_pereach){
            tempNormal = true
          }else{
            tempNormal = false
          }
          if(query.filter_repeat_attack == "atk"){
            // Filter and get chars without attack
            return tempNormal
          }
        }else{
          tempNormal = false
        }
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0){
          if(Object.hasOwn(char.combat_skills[stringfilter].repeat_attack, "perEachEffect_n").toString() == query.has_pereach
          && char.combat_skills[stringfilter].repeat_attack.repeat_has_attack == true){
            tempRepeat = true
          }else{
            tempRepeat = false
          }
          if(query.filter_repeat_attack == "repeat"){
            // Filter and get chars without attack
            return tempRepeat
          }
        }
        output = tempNormal || tempRepeat
        if(Object.keys(char.combat_skills[stringfilter].attack).length == 0 && query.filter_repeat_attack == "atk"){
          return false
        }
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length == 0 && query.filter_repeat_attack == "repeat"){
          return false
        }
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0 
        && char.combat_skills[stringfilter].repeat_attack.repeat_has_attack == false
        && query.filter_repeat_attack == "repeat"
        ){
          return false
        }
        return output
      }else{
        return true
      }
    },
    filterEffect(char, query, stringfilter){
      if(query.ef_target || query.ef || query.ef_val1 || query.ef_val2 || query.filter_repeat_effect){
        let temp = false;
        if(Object.keys(char.combat_skills[stringfilter].effects).length != 0){
          for (let i = 0; i < char.combat_skills[stringfilter].effects.n_effects; i++){
            let temp2 = true
            temp2 &&= (query.ef_target ? char.combat_skills[stringfilter].effects[(i+1).toString() + "targets"].toLowerCase() == query.ef_target : true)
            temp2 &&= (query.ef ? char.combat_skills[stringfilter].effects[(i+1).toString() + "effect"].toLowerCase().replace(/ /g, "_") == query.ef : true)
            temp2 &&= (query.ef_val1 ? parseInt(char.combat_skills[stringfilter].effects[(i+1).toString() + "value"]) >= parseInt(query.ef_val1) : true)
            temp2 &&= (query.ef_val2 ? parseInt(char.combat_skills[stringfilter].effects[(i+1).toString() + "value"]) <= parseInt(query.ef_val2) : true)
            temp ||= temp2
          //  if(char.id_adventurers == "raul_4"){
          //     console.log(temp2)
          //     console.log(char.combat_skills[stringfilter].effects[(i+1).toString() + "targets"].toLowerCase())
          //   }
          }
          if(query.filter_repeat_effect == "atk"){
            // Filter and get chars with effect in normal action
            return temp
          }
        } else {
          temp = false
        }
        let temprepeat = false;
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0){
          if(Object.hasOwn(char.combat_skills[stringfilter].repeat_attack, "effects")){
            for (let j = 0; j < char.combat_skills[stringfilter].repeat_attack.effects.n_effects; j++){
              let temp2 = true
              temp2 &&= (query.ef_target ? char.combat_skills[stringfilter].repeat_attack.effects[(j+1).toString() + "targets"].toLowerCase() == query.ef_target : true)
              temp2 &&= (query.ef ? char.combat_skills[stringfilter].repeat_attack.effects[(j+1).toString() + "effect"].toLowerCase().replace(/ /g, "_") == query.ef : true)
              temp2 &&= (query.ef_val1 ? parseInt(char.combat_skills[stringfilter].repeat_attack.effects[(j+1).toString() + "value"]) >= parseInt(query.ef_val1) : true)
              temp2 &&= (query.ef_val2 ? parseInt(char.combat_skills[stringfilter].repeat_attack.effects[(j+1).toString() + "value"]) <= parseInt(query.ef_val2) : true)
              temprepeat ||= temp2
            }
          }else{
            temprepeat = false  
          }
          if(query.filter_repeat_effect == "repeat"){
            // Filter and get chars with effect in repeat
            return temprepeat
          }
          // console.log(char.combat_skills[stringfilter].effects != {})
        } else {
          temprepeat = false
        }

        if(Object.keys(char.combat_skills[stringfilter].effects).length == 0 && query.filter_repeat_effect == "atk"){
          return false
        }
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length == 0){
          if(!Object.hasOwn(char.combat_skills[stringfilter].repeat_attack, "effects") && query.filter_repeat_effect == "repeat"){
            return false
          }
        }
        return temp ||= temprepeat
      }else{
        return true
      }
    },
    filterComb(char, query){
      if(query.comb_skill){
        if(char.combination_attack.has_combination){
          // if(char.name=="Riveria Ljos Alf"){
          //   console.log(char.title)
          // }
          let temp2 = false
          for (let i = 0; i < char.combination_attack.n_combination; i++){
            temp2 ||= char.combination_attack["combination_attack_" + (i+1).toString()].toLowerCase().replace(/ /g, "_") == query.comb_skill
            // if(char.name=="Riveria Ljos Alf"){
            //   console.log(char.combination_attack["combination_attack_" + (i+1).toString()].toLowerCase().replace(/ /g, "_"))
            //   console.log(temp2)
            // }
          }
          return temp2
        }else{
          return false
        }
      }else{
        return true
      }
    },
    filterKill(char, query){
      if(query.killer){
        if(char.killer){
          let temp2 = false
          if(char.killer == query.killer){
            temp2 = true
          }
          return temp2
        }else{
          return false
        }
      }else{
        return true
      }
    },
    filterChar(char, query){
      let temp = true;

      temp &&= (query.char ? char.name.toLowerCase().replace(/ /g, "_").replace("(", "").replace(")","") == query.char : true)
      temp &&= (query.star ? char.stars.toString() == query.star : true)
      // temp &&= (query.timelimited ? char.timelimited == (
      //   query.timelimited.toLowerCase() === 'true' ? true : false
      // ) : true)
      temp &&= (query.timelimited ? char.time_limited.toString() == (query.timelimited) : true)
      temp &&= (query.free ? char.is_free.toString() == query.free : true)
      temp &&= (query.element ? char.type_element.toString() == query.element : true)
      temp &&= (query.heroasc ? char.have_hero_asc.toString() == query.heroasc : true)

      if(query.attacktype){
        let temp2 = false;
        switch(query.attacktype){
          case "physical_type":
            temp2 ||= char.type_attack == "physical_type"
            temp2 ||= char.type_attack == "phys_plus"
            temp2 ||= char.type_attack == "phys_festa"
            break;
          case "magic_type":
            temp2 ||= char.type_attack == "magic_type"
            temp2 ||= char.type_attack == "mag_plus"
            temp2 ||= char.type_attack == "mag_festa"
            break;
          case "balance_type_all":
            temp2 ||= char.type_attack == "balance_type"
            temp2 ||= char.type_attack == "balance_type_old"
            temp2 ||= char.type_attack == "bala_phys"
            temp2 ||= char.type_attack == "bala_plus_phys"
            temp2 ||= char.type_attack == "bala_mag"
            temp2 ||= char.type_attack == "bala_plus_mag"
            break;
          case "balance_type":
            temp2 ||= char.type_attack == "balance_type"
            temp2 ||= char.type_attack == "balance_type_old"
            break;
          case "bala_phys":
            temp2 ||= char.type_attack == "bala_phys"
            temp2 ||= char.type_attack == "bala_plus_phys"
            break;
          case "bala_mag":
            temp2 ||= char.type_attack == "bala_mag"
            temp2 ||= char.type_attack == "bala_plus_mag"
            break;
          case "healer_type":
            temp2 ||= char.type_attack == "healer_type"
            break;
          case "defense_type":
            temp2 ||= char.type_attack == "defense_type"
            break;
          default:
            temp2 ||= char.type_attack == query.attacktype
            break;
        }
        temp &&= temp2 
      }
      return temp
    },
    filterDev(char, query){
      let temp = true;

      let temp2 = false;
      let temp3 = false;
      let temp4 = false;
      let temp5 = false;

      for (let i = 0; i < 5; i++){

        temp3 ||= (query.devname ? char.dev_abilities[i]["name"].toLowerCase().replace(/ /g, "_") == query.devname : false)

        for (let j = 0; j < char.dev_abilities[i].n_efeitos; j++) {

          temp2 ||= (query.devskill ? char.dev_abilities[i]["effect_" + (j+1).toString()].toLowerCase().replace(/ /g, "_") == query.devskill : false)
          temp4 ||= (query.dev_valmin ? char.dev_abilities[i]["value_" + (j+1).toString()].replace("+", "").replace("%", "") <= query.dev_valmin : false) 
          temp5 ||= (query.dev_valmax ? char.dev_abilities[i]["value_" + (j+1).toString()].replace("+", "").replace("%", "") >= query.dev_valmax : false) 

        }
      }
      if(query.devskill){
        temp &&= temp2;
      }
      if(query.devname){
        temp &&= temp3;
      }
      if(query.dev_valmin){
        temp &&= temp4;
      }
      if(query.dev_valmax){
        temp &&= temp5;
      }

      return temp
    },
    filterRepeat(char, query, stringfilter){
      let temp = true
      if(query.n_repeat || query.repeat_hasattack || query.repeat_haseffect){
        if(Object.keys(char.combat_skills[stringfilter].repeat_attack).length != 0){
          
          temp &&= (query.n_repeat ? char.combat_skills[stringfilter].repeat_attack.repeatCount == query.n_repeat : true)
          temp &&= (query.repeat_hasattack ? char.combat_skills[stringfilter].repeat_attack.repeat_has_attack == (query.repeat_hasattack === 'true') : true)
          if(query.repeat_haseffect){
            temp &&= (Object.hasOwn(char.combat_skills[stringfilter].repeat_attack, 'effects') == (query.repeat_haseffect === 'true'))
          }

          return temp
        }else{
          return false
        }
      }else{
        return true
      }
    },
    searchFilter(filtered = this.dataAdv){
      this.filterModal = false;
      let query = this.$route.query;

      let characters = filtered.filter((char) => {
        let skillsp = this.filterAttack(char, query, "special")
        let skill1 = this.filterAttack(char, query, "skill1")
        let skill2 = this.filterAttack(char, query, "skill2")
        let skill3 = this.filterAttack(char, query, "skill3")
      
        skillsp &&= this.filterPerEach(char, query, "special")
        skill1 &&= this.filterPerEach(char, query, "skill1")
        skill2 &&= this.filterPerEach(char, query, "skill2")
        skill3 &&= this.filterPerEach(char, query, "skill3")
        
        skillsp &&= this.filterHasPerEach(char, query, "special")
        skill1 &&= this.filterHasPerEach(char, query, "skill1")
        skill2 &&= this.filterHasPerEach(char, query, "skill2")
        skill3 &&= this.filterHasPerEach(char, query, "skill3")

        skillsp &&= this.filterEffect(char, query, "special")
        skill1 &&= this.filterEffect(char, query, "skill1")
        skill2 &&= this.filterEffect(char, query, "skill2")
        skill3 &&= this.filterEffect(char, query, "skill3")

        skillsp &&= this.filterRepeat(char, query, "special")
        skill1 &&= this.filterRepeat(char, query, "skill1")
        skill2 &&= this.filterRepeat(char, query, "skill2")
        skill3 &&= this.filterRepeat(char, query, "skill3")

        if(this.disabledCheck[0]){
          skillsp &&= this.checkboxSkill[0]
          skill1 &&= this.checkboxSkill[1]
          skill2 &&= this.checkboxSkill[2]
          skill3 &&= this.checkboxSkill[3]
        }
        if(this.disabledCheck[1]){
          skillsp &&= this.checkboxSkillEffects[0]
          skill1 &&= this.checkboxSkillEffects[1]
          skill2 &&= this.checkboxSkillEffects[2]
          skill3 &&= this.checkboxSkillEffects[3]
        }

        let skill_combo = (skillsp || skill1 || skill2 || skill3) //&& this.filterAttackAndRepeat(char, query)
        let combination_combo = this.filterComb(char, query)
        let killer = this.filterKill(char, query)
        let char_filter = this.filterChar(char, query)
        let dev = this.filterDev(char, query)

        // if(skill_combo && combination_combo && killer && char_filter && dev){
        //   console.log(char.id_adventurers)
        // }
        return (skill_combo && combination_combo && killer && char_filter && dev)
      })

      let temp_char
      if(query.sort){
        if(query.asc){
            if(query.asc == "ascending"){
              temp_char = characters.sort((a, b) => a[query.sort] - b[query.sort])
            }else{
              temp_char = characters.sort((a, b) => b[query.sort] - a[query.sort])    
            }
        }else{
          temp_char = characters.sort((a, b) => b[query.sort] - a[query.sort])
        }
      }else{
        temp_char = characters
      }
      console.log(temp_char.length)
      this.isFirstSearch = false;
      this.filteredData = temp_char
      // this.getDisplay()
    },
    decideBase(star, hero_asc, hero_festa){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/frame_1.png')
          case 2:
            return require('../assets/frame/frame_2.png')
          case 3:
            return require('../assets/frame/frame_3.png')  
          case 4:
            if(hero_festa){
              return require('../assets/frame/frame_4_festa.png')
            }
            if(!hero_asc){return require('../assets/frame/frame_4.png')}
            else{return require('../assets/frame/frame_4_star.png')}
          default:
            return require('../assets/frame/frame_1.png')    
        }
      }
    },
    decideBaseFrame(star, hero_asc, hero_festa){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/base_frame_1.png')
          case 2:
            return require('../assets/frame/base_frame_2.png')
          case 3:
            return require('../assets/frame/base_frame_3.png')  
          case 4:
            if(hero_festa){
              return require('../assets/frame/frame_4_festa.png')
            }
            if(!hero_asc){return require('../assets/frame/base_frame_4.png')}
            else{return require('../assets/frame/base_frame_4_star.png')}
          default:
            return require('../assets/frame/base_frame_1.png')    
        }
      }
    },
    topFunction(){
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    }
  },
}
</script>

